import React from 'react'
import './index.less'
import NoneData from 'components/NoneData/all-none'
import TitleBar from 'components/topbar'
import connect from 'connect'
import {  Button, WhiteSpace, WingBlank, List, InputItem, Flex, Switch, Picker, Modal, PickerView } from 'antd-mobile';
import { createForm } from 'rc-form';
@connect
class Home extends React.Component {
    constructor(props){
        super(props);
        this.state = {
          value: null,
          
        }
    }
    
    componentDidMount () {
       
    }

    handleClick = () => {
        this.inputRef.focus();
    }
  onChange = (value) => {
    console.log(value);
    this.setState({
      value,
    });
  }
  onScrollChange = (value) => {
    console.log(value);
  }

  showModal = key => (e) => {
    e.preventDefault(); // 修复 Android 上点击穿透
    this.setState({
      [key]: true,
    });
  }
  onClose = key => () => {
    this.setState({
      [key]: false,
    });
  }


    render () {
        const Item = List.Item;
        const Brief = Item.Brief;
        let SwitchExample = (props) => {
          const { getFieldProps } = props.form;
          return (
            <List>
              <InputItem
                {...getFieldProps('autofocus')}
                clear
                placeholder="请输入原手机号码"
                ref={el => this.inputRef = el}
              ></InputItem>
              <InputItem
                clear
                {...getFieldProps('focus')}
                placeholder="请输入新手机号"
                ref={el => this.inputRef = el}
              ></InputItem>
              <Flex>
                <InputItem
                  clear
                  {...getFieldProps('focus')}
                  placeholder="请输入短信验证码"
                  ref={el => this.inputRef = el}
                ></InputItem>
                <Button>发送验证码</Button>
              </Flex>
            </List>
          );
        };
        SwitchExample = createForm()(SwitchExample);
        return (
            <div className="address-container">
            <TitleBar title="修改密码"/>
            <SwitchExample/>
            <div className="cardfooter">
                <Button>确定</Button>
            </div>
     </div>
        )
    }
}
export default Home


